.form-label {
  display: inline-block;
  margin-right: 15px;
}

.deliveryInstructions {
  margin: auto;
}

.delivery-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
}

.form-wrapper {
  border: 1px solid black;
  padding: 15px;
  align-self: baseline;
}

.delivery-cart-wrapper {
  display: inline-block;
  border: 1px solid black;
  padding: 30px;
}

.delivery-cart-title {
  margin-top: 0;
}

.delivery-input {
  float: right;
}

.delivery-cart-container {
  margin-right: 30px;
  margin-bottom: 30px;
}

.submit {
  margin-right: 30px;
  margin-left: 15px;
}

.button-wrapper {
    text-align: center;
}

@media only screen and (min-width: 1024px) {
  .delivery-wrapper {
    flex-wrap: nowrap;
    justify-content: space-between;
    width: 83%;
    margin-bottom: 30px;
  }
  .deliveryInstructions {
    width: 83%;
  }
  .delivery-cart-container {
    margin-right: 0;
  }
}

@media only screen and (min-width: 1224px) {
  .delivery-wrapper {
    width: 70%;
  }
  .deliveryInstructions {
    width: 70%;
  }
}
